import {Link} from "react-router-dom";

export const GooglePlay = () => {


    return <div className="text-start w-75 bg-white my-5 p-5 rounded-4">
        <p>Приложение в Google Play находиться на этапе раннего доступа и
            для его публикации нам необходимо зарегистрировать 20 человек в программе тестирования.
            Присоединитесь к команде тестирования, оставив заявку и <span className="fw-bold">получите 1 месяц бесплатного использования</span>.</p>

        <p className="fw-bold fs-6 text-center">
            ПРОВОДИТЬ КАКОЕ-ЛИБО ТЕСТИРОВАНИЕ НЕОБЯЗАТЕЛЬНО, ОТ ВАС НУЖНА ТОЛЬКО РЕГИСТРАЦИЯ В ТЕСТИРОВАНИИ.
        </p>

        <p>Для того, чтобы принять участие в тестировании вам нужно выполнить 2 простых пункта:</p>

        <ul>
            <li>Пройти <Link className="text-black" to="/register">регистрацию</Link>, активировать аккаунт и войти в
                него.
            </li>
            <li>Создать обращение в тех. поддержку с категорией "Прочее".</li>
        </ul>

        <p>
            В обращении укажите адрес электронной почты, который используется в аккаунте Google Play
            и через пару часов вы получите доступ к нашему приложению (Вы получите уведомление на почту и сообщение в обращении).
        </p>
    </div>
}