import {Button, ModalBody, ModalFooter, ModalHeader, ModalTitle} from "react-bootstrap";
import {closeLastModal} from "../../../../../component/modal/modalStore";

export  const OfferAgreementModal = () => {
    return <>
        <ModalHeader className="px-5">
            <ModalTitle>Договор оферты</ModalTitle>
        </ModalHeader>
        <ModalBody className="px-5">
            <div className="text-start">
                <p className="fw-bold mt-3">1. Общие Положения:</p>
                <ul>
                    <li>
                        Настоящий договор оферты (далее - "Оферта") представляет собой публичное предложение с самозанятым Кузнецовым Юрием Рустемовичем ИНН 645295747886 (далее - "Поставщик услуг") о предоставлении доступа к услугам виртуальной частной сети (VPN).
                    </li>
                    <li>
                        Акцепт Оферты осуществляется Клиентом через оплату услуг согласно тарифам, указанным на сайте Поставщика услуг.
                    </li>
                </ul>

                <p className="fw-bold mt-3">2. Предмет Оферты:</p>
                <ul>
                    <li>
                        Поставщик услуг предоставляет Клиенту неконкурентные, неисключительные права на использование VPN-сервиса в соответствии с выбранным тарифным планом.
                    </li>
                    <li>
                        Клиент обязуется использовать VPN-сервис в личных, некоммерческих целях.
                    </li>
                </ul>

                <p className="fw-bold mt-3">3. Условия Оплаты:</p>
                <ul>
                    <li>Оплата услуг производится Клиентом в соответствии с выбранным тарифным планом.</li>
                    <li>В случае изменения тарифов, новые цены вступают в силу с момента следующего платежного периода.</li>
                </ul>

                <p className="fw-bold mt-3">4. Права и Обязанности Сторон:</p>
                <ul>
                    <li>Клиент имеет право на доступ к VPN-сервису 24/7, за исключением времени на техническое обслуживание.</li>
                    <li>Поставщик услуг обязуется предоставлять услуги в соответствии с техническими параметрами, указанными на сайте.</li>
                </ul>

                <p className="fw-bold mt-3">5. Ответственность Сторон:</p>
                <ul>
                    <li>Стороны несут ответственность за неисполнение или ненадлежащее исполнение своих обязательств в
                        соответствии с действующим законодательством.
                    </li>
                </ul>


                <p className="fw-bold mt-3">6. Срок Действия и Изменение Оферты:</p>
                <ul>
                    <li>
                        Оферта вступает в силу с момента ее акцепта Клиентом и действует до момента отказа Клиента от услуг.
                    </li>
                    <li>
                        Поставщик услуг имеет право вносить изменения в Оферту с предварительным уведомлением Клиентов не менее чем за 30 дней.
                    </li>
                </ul>

                <p className="fw-bold mt-3">7. Конфиденциальность и Защита Персональных Данных:</p>
                <ul>
                    <li>Поставщик услуг обязуется не разглашать персональные данные Клиента и не использовать их вне рамок, предусмотренных Офертой.</li>
                </ul>

                <p className="fw-bold mt-3">8. Расторжение Оферты:</p>
                <ul>
                    <li>Клиент вправе расторгнуть Оферту в одностороннем порядке, уведомив Поставщика услуг не позднее чем за 7 дней до желаемой даты расторжения.</li>
                </ul>
            </div>
        </ModalBody>
        <ModalFooter>
            <Button variant="dark" onClick={closeLastModal}>
                Закрыть
            </Button>
        </ModalFooter>
    </>
}