import {useFormContext} from "react-hook-form";
import {Badge, FormControl, FormGroup, FormLabel, InputGroup} from "react-bootstrap";
import {ISetPasswordInputs} from "./ISetPasswordInputs";
import {observer} from "mobx-react-lite";
import {getTranslate} from "../../../utils/localization";

export const SetPasswordInputs = observer((props: ISetPasswordInputs) => {
    const {register, watch, formState: {errors}} = useFormContext();

    return <>
        <FormGroup className="mb-2" {...props.attributes}>
            <div className="d-block d-lg-flex">
                <FormLabel className="me-2">
                    {`${getTranslate("Пароль")}`}:
                </FormLabel>
                {errors["password"]?.message &&
                    <div>
                        <Badge bg="warning">{errors["password"].message.toString()}</Badge>
                    </div>
                }
            </div>

            <FormControl {...register("password", {
                required: {value: props.isRequired, message: `${getTranslate("Поле обязательно к заполнению")}.`},
                minLength: {value: 6, message: `${getTranslate("Минимальная длина пароля 6 символов")}.`},
                maxLength: {value: 24, message: `${getTranslate("Максимальная длина пароля 24 символа")}.`},
            })} type="password" placeholder={`${getTranslate("Введите пароль")}`}/>
        </FormGroup>
        <FormGroup className="mb-2" {...props.attributes}>
            <div className="d-block d-lg-flex">
                <FormLabel className="me-2">
                    {`${getTranslate("Подтвердите пароль")}`}:
                </FormLabel>
                {errors["confirmPassword"]?.message &&
                    <div>
                        <Badge bg="warning">{errors["confirmPassword"].message.toString()}</Badge>
                    </div>
                }
            </div>

            <FormControl {...register("confirmPassword", {
                required: {value: props.isRequired, message: `${getTranslate("Поле обязательно к заполнению")}.`},
                validate: (value) => {
                    if(watch("password") !== value)
                        return `${getTranslate("Введенные пароли не совпадают")}.`
                }
            })} type="password" placeholder={`${getTranslate("Повторите пароль")}`}/>
        </FormGroup>
    </>
})