import {Button, ModalBody, ModalFooter, ModalHeader, ModalTitle} from "react-bootstrap";
import {closeLastModal} from "../../../../../component/modal/modalStore";

export const TermsOfReturnModal = () => {
    return <>
        <ModalHeader className="px-5">
            <ModalTitle>Условия возврата</ModalTitle>
        </ModalHeader>
        <ModalBody className="px-5">
            <div className="text-start">
                <p className="fw-bold mt-3">1. Общие Положения:</p>
                <ul>
                    <li>
                        Вы можете запросить возврат средств, если полученные услуги были некачественными или не
                        предоставлены в соответствии с условиями заказа.
                    </li>
                    <li>
                        Возврат средств за неиспользованное время подписки возможен в случае досрочного расторжения
                        договора со стороны пользователя.
                    </li>
                    <li>
                        Возврат рассчитывается исходя из фактически неиспользованного времени услуги.
                    </li>
                </ul>

                <p className="fw-bold mt-3">2. Процедура Возврата:</p>
                <ul>
                    <li>Для запроса возврата, свяжитесь с нашей службой поддержки по указанным контактным данным. Мы
                        рассмотрим ваш запрос и произведем возврат средств на вашу карту/кошелек.
                    </li>
                    <li>Запрос на возврат должен быть подан не позднее 30 дней с момента прекращения использования
                        услуги.
                    </li>
                </ul>

                <p className="fw-bold mt-3">3. Расчет Возврата:</p>
                <ul>
                    <li>Возврат средств производится пропорционально количеству полных неиспользованных месяцев
                        подписки.
                    </li>
                    <li>В случае месячной подписки, неиспользованные дни в текущем месяце не подлежат возврату.</li>
                </ul>

                <p className="fw-bold mt-3">4. Исключения:</p>
                <ul>
                    <li>Возврат средств не предусмотрен для подписок со скидкой или проведенных в рамках акционных
                        предложений.
                    </li>
                    <li>В случае нарушения условий использования сервиса, возврат средств не производится.</li>
                </ul>
                <p className="fw-bold mt-3">5. Сроки возврата:</p>
                <ul>
                    <li>Мы постараемся рассмотреть ваш запрос в кратчайшие сроки.
                    </li>
                </ul>
            </div>
        </ModalBody>
        <ModalFooter>
            <Button variant="dark" onClick={closeLastModal}>
                Закрыть
            </Button>
        </ModalFooter>
    </>
}